.user-selection-btn{

  @apply hover:bg-primary hover:text-primary rounded-lg p-2 hover:scale-105 transition-all duration-100 ease-in-out 
}

.modal{
  @apply  hidden fixed z-10 left-0 top-0 w-full h-full overflow-auto bg-opacity-50
}



